const genderReducer = (state = [], action) => {
    switch(action.type) {
        case "BOY":
            return state = [1,3,4,""];
        case "GIRL":
            return state = [2,3,4,""];
        case "ALL":
            return state = [0,0,0,""];
        default:
            return state;
    }
}

export default genderReducer;




