import React from 'react';
import { useDispatch } from "react-redux";
import { character, brand } from "../../actions";
import { useNavigate } from "react-router-dom";
import { Button } from '@chakra-ui/react';
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Pre_select = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // 
  const character_handleClick = () => {
    dispatch(character()); // characterdispatch
    navigate(`${homeUrl}/pregender`); // 
  }

  // 
  const brand_handleClick = () => {
    dispatch(brand()); // branddispatch
    navigate(`${homeUrl}/pregender`); // 
  }

  return (
    <>
      <br/>
      <h2></h2>
      <br/>
      <Button onClick={() => character_handleClick()} style={{ fontSize: '2.5em' }} className='characterButton'></Button>
      <Button onClick={() => brand_handleClick()} style={{ fontSize: '2.5em' }} className='brandButton'></Button>
      <br/>
      <br/>
      <br/>
      <div>
        <br/>
        <Button onClick={() => navigate(`${homeUrl}/firstchoice`)} style={{ fontSize: '1.5em' }}></Button>
      </div>
    </>
  );
}

export default Pre_select;
