const ageReducer = (state = [], action) => {
    switch(action.type) {
        case "AGE1":
            return state = [4,6,""];
        case "AGE2":
            return state = [6,8,""];
        case "AGE3":
            return state = [8,10,""];
        case "AGE4":
            return state = [10,12,""];
        case "AGE5":
            return state = [1,15,""];
        default:
            return state;
    }
}

export default ageReducer;


